@import url(https://fonts.googleapis.com/css?family=Vidaloka&display=swap);
body {
  background-color: #f7f7f7;
  margin: 0px; }

a {
  text-decoration: none; }

.back-drop {
  background-color: rgba(0, 0, 0, 0.2);
  height: 100vh;
  left: 0px;
  position: fixed;
  top: 0px;
  width: 100vw;
  z-index: 100; }

#App {
  padding: 45px 0; }
  #App .header {
    background-color: #f7f7f7;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    display: flex;
    height: 45px;
    justify-content: space-between;
    padding: 0 15px;
    position: fixed;
    text-align: center;
    top: 0px;
    width: calc(100% - 30px);
    z-index: 0; }
    #App .header img {
      height: 20px;
      margin: 12px 0; }
    #App .header h3 {
      font-family: 'Vidaloka', serif;
      color: #3d3d3d;
      margin: 11px 0; }
  #App .news-grids {
    display: flex;
    flex-wrap: wrap;
    font-family: sans-serif;
    justify-content: space-evenly;
    padding: 15px 0;
    text-align: center; }
    #App .news-grids .news-grid {
      background-color: #fff;
      border-radius: 10px;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      display: flex;
      height: 15vh;
      margin: 10px 20px;
      overflow: hidden;
      width: calc(100vw - 40px); }
      #App .news-grids .news-grid .news-img {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;
        width: 40%; }
      #App .news-grids .news-grid .news-content {
        color: #000;
        padding: 0 10px;
        width: 60%; }
        #App .news-grids .news-grid .news-content .news-title {
          font-size: 13px;
          height: calc(100% - 47px);
          margin: 10px 0;
          text-align: left;
          overflow: hidden; }
        #App .news-grids .news-grid .news-content .news-caption {
          display: flex;
          font-size: 12px;
          justify-content: space-between; }
    #App .news-grids .news-status {
      font-size: 15px;
      margin-bottom: 20px;
      width: 100%; }
      #App .news-grids .news-status.loading:after {
        content: ' .';
        -webkit-animation: dots 1s steps(5, end) infinite;
                animation: dots 1s steps(5, end) infinite; }
  #App .footer {
    background-color: #f7f7f7;
    bottom: 0px;
    display: flex;
    height: 45px;
    padding: 5px;
    position: fixed;
    width: 100%; }
    #App .footer .icon {
      flex: 0 0 20px;
      margin: 12px 15px;
      text-align: center; }
      #App .footer .icon svg {
        height: 20px; }
    #App .footer .search-bar {
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: calc(100% - 130px);
      padding: 0 10px;
      position: relative; }
      #App .footer .search-bar .search-input {
        border: none;
        border-radius: 20px;
        height: 24px;
        margin: 10px 0;
        padding: 0 35px 0 15px;
        width: calc(100% - 50px); }
        #App .footer .search-bar .search-input:focus {
          outline: none; }
      #App .footer .search-bar .icon {
        position: absolute;
        right: 10px;
        top: 0px; }
        #App .footer .search-bar .icon svg {
          height: 12px;
          width: 12px; }

.list {
  background-color: #f7f7f7;
  border-radius: 20px 20px 0 0;
  bottom: 0px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  height: 50vh;
  left: 0px;
  position: fixed;
  width: 100%;
  z-index: 100; }
  .list .list-block {
    height: calc(100% - 40px);
    margin: 25px 20px;
    overflow: scroll;
    -webkit-overflow-scrolling: touch; }
    .list .list-block::-webkit-scrollbar {
      display: none; }
  .list .list-item {
    display: flex;
    padding: 15px 0; }
    .list .list-item span {
      font-size: 18px; }
      .list .list-item span.selected {
        font-weight: bold; }
    .list .list-item svg {
      height: 25px;
      margin-right: 20px; }
    .list .list-item:not(:last-child) {
      border-bottom: 1px solid #dadada; }

.list-enter {
  opacity: 0.01; }
  .list-enter .list {
    max-height: 0px; }

.list-enter-active {
  opacity: 1;
  -webkit-transition: all .3s;
  transition: all .3s; }
  .list-enter-active .list {
    max-height: 50vh;
    -webkit-transition: all .3s;
    transition: all .3s; }

.list-exit {
  opacity: 1; }
  .list-exit .list {
    max-height: 50vh;
    -webkit-transition: all .3s;
    transition: all .3s; }

.list-exit-active {
  opacity: 0.01;
  -webkit-transition: all 300ms;
  transition: all 300ms; }
  .list-exit-active .list {
    max-height: 0px; }

@-webkit-keyframes dot-keyframes {
  0% {
    opacity: .4;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); }
  50% {
    opacity: 1;
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2); }
  100% {
    opacity: .4;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); } }

@keyframes dot-keyframes {
  0% {
    opacity: .4;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); }
  50% {
    opacity: 1;
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2); }
  100% {
    opacity: .4;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1); } }

.loading-dots {
  text-align: center;
  margin-bottom: 20px;
  width: 100%; }
  .loading-dots--dot {
    -webkit-animation: dot-keyframes 1.5s infinite ease-in-out;
            animation: dot-keyframes 1.5s infinite ease-in-out;
    background-color: #000;
    border-radius: 10px;
    display: inline-block;
    height: 10px;
    width: 10px; }
    .loading-dots--dot:nth-child(2) {
      -webkit-animation-delay: .5s;
              animation-delay: .5s;
      margin: 0 10px; }
    .loading-dots--dot:nth-child(3) {
      -webkit-animation-delay: 1s;
              animation-delay: 1s; }

